@font-face {
    font-family: "IBM";
    src: url("./Assets/Fonts/IBMPlexSansArabic-SemiBold.ttf");
}
@font-face {
    font-family: "IBM Light";
    src: url("./Assets/Fonts/IBMPlexSansArabic-Medium.ttf");
}
@font-face {
    font-family: "IBM Regular";
    src: url("./Assets/Fonts/IBMPlexSansArabic-Regular.ttf");
}


::-webkit-scrollbar,
::-webkit-scrollbar-track {
    width: 0;
}

.App {
    background: url(./Assets/Images/bg.svg) no-repeat;
    background-size: cover;
    background-position: center center;
    animation: change 50s ease-in-out infinite;
}

.Overlay {
    background: linear-gradient(180deg, rgb(13 65 83 / 88%) 9.64%, rgb(0 131 175 / 55%) 82.59%);
}

@keyframes change {
    0% {
        background-size: cover;
        background-position: 100% 22%;
    }
    50% {
        background-size: cover;
        background-position: 50% 22%;
    }
    100% {
        background-size: cover;
        background-position: 100% 22%;
    }
}

@media screen and (min-height: 250px) {
    .Overlay {
        height: 100% !important;
    }
}

@media screen and (min-height: 350px) {
    .Overlay {
        height: 100% !important;
    }
}

@media screen and (min-width: 391px) {
    .Overlay {
        height: 100vh;
    }
}

@media screen and (min-height: 599px) {
    .Overlay {
        height: 100%;
    }
}



@media screen and (min-width: 610px) {

    .App {
        animation: change 40s linear infinite;
    }

    .Overlay {
        gap: 4.1rem;
    }

    .Hero {
        padding-top: 3rem !important;
    }
    
    .SocialLinks {
        width: 40rem;
        padding: 3rem 3rem !important;
        flex-direction: row !important;
    }

    a {
        width: 15rem !important;
    }

    .MainImage img {
        width: 46rem;
        height: 25rem;
    }

    .Hero img {
        width: 20rem;
    }
    
    .Hero .HeroText {
        width: fit-content;
    }


    .Left {
        margin-top: 0 !important;    
    }
}

@media screen and (min-height: 719px) {
    .Overlay {
        height: 100%;
    }
}

@media screen and (min-height: 740px) {
    .Overlay {
        height: 100%;
    }
}

@media screen and (min-height: 840px) {
    .Overlay {
        height: 100vh !important;
    }
}

@media screen and (min-height: 890px) {
    .Overlay {
        height: 100vh !important;
    }
}


@media screen and (min-width: 1536px) {
    @keyframes changePc {    
        0% {
            background-size: cover;
            background-position: 100% 22%;
        }
        50% {
            background-size: cover;
            background-position: 100% 100%;
        }
        100% {
            background-size: cover;
            background-position: 100% 22%;
        }
    }

    .App {
        animation: changePc 15s linear infinite;
    }
}