.SocialLinks {
    box-shadow: 0px 8px 20px 5px rgba(72, 209, 255, 0.5);
}


.SocialContainer {
    transition: all .2s ease-in-out;
}

a:hover .SocialContainer {
    transform: scale(1.02);     
    box-shadow: 0px 8px 20px 5px rgba(72, 209, 255, 0.5);
    background: #336066;
    color: #fff;
}
a:hover .SocialIcon {
    color: #fff ;
}
a:hover .SocialIcon circle {
    fill: #fff !important;
}
a:hover .SocialIcon g path {
    fill: #336066 !important;
}
a:hover .SocialIconWa g {
    fill: #fff !important;
}
a:hover .SocialIconInsta rect {
    fill: #fff;
}
a:hover .SocialIconInsta path {
    fill: #336066;
}

a {
    font-family: "IBM Light";
}

.insta {
    direction: ltr;
}